import React, { Component } from 'react';
import PropTypes from 'prop-types';
import '../res/css/main.css';
import '../res/css/animate.min.css';
import '../res/css/bootstrap.min.css';
import '../res/css/lightbox.css';
import '../res/css/main.css';
import '../res/css/responsive.css';
import '../App.css';
import Utils from '../constants/Utils';

class Main extends Component{

    constructor(props){
        super(props);
        this.state = {
            isLogin: false,
        };
    }
    
    static get defaultProps() {
        return {
            startBtnListener: () => {},
            loginWithAddressListener: () => {},
        }
    }

    static propTypes(){
        startBtnListener: PropTypes.func.isRequired;
        loginWithAddressListener: PropTypes.func.isRequired;
    }

    inputBox() {
        return (
            <div>
                <div class="form-group">
                    <p className="css_main_text_title" style={{margin:0}}>([n1])</p>
                    <input class="css_input css_search_input" type="text" name="address" ref="address" 
                        required="required" autoFocus={true} placeholder="n1"/>
                </div>
                <div><a href="https://blog.nebulas.io/2018/04/12/creating-a-nas-wallet/" target="_blank"></a></div>

                 <div className="css_inputbox_div">
                    <input type="submit" name="query" class=" css_home_button css_home_button_search" value="" 
                    onClick={()=>{
                        var walletAddress = this.refs.address.value;
                        console.log("click display button inputbox value= "+walletAddress);
                        if(walletAddress === null || walletAddress === "" || walletAddress === undefined) {
                            alert("");
                            return;
                        }
                        this.props.loginWithAddressListener(walletAddress);

                        
                    }}/>
                    <input type="submit" name="back" class=" css_home_button css_home_button_search" value="" 
                    onClick={()=>{
                        this.setState({isLogin:false})
                        
                    }}/>
                </div>
                <div style= {{marginTop: 10, marginLeft: 30}} id="loading_container"></div>
            </div>
        )
    }
    render(){
        const{
            startBtnListener,
            loginWithAddressListener,
        }=this.props;
        return (
            <div>
                {/* <div class="navbar navbar-inverse" role="banner" >
                    <div class="container">
                        <div class="collapse navbar-collapse">
                                <ul class="nav navbar-nav navbar-right">
                                    <li class="active"><a href="home-slider"></a></li>
                                    <li class="active"><a href="#about" class="learn-btn animated fadeInUp"> <i class="fa fa-arrow-down"></i></a></li>         
                                    <li class="active"><a href="https://blog.nebulas.io/2018/04/12/creating-a-nas-wallet/" target="_blank" class="learn-btn animated fadeInUp"> <i class="fa fa-arrow-down"></i></a></li>         
                                    <li class="active"><a href="#footer" class="learn-btn animated fadeInUp"><i class="fa fa-arrow-down"></i></a></li>         
                                                        
                                </ul>
                            </div>
        
                    </div>
                </div> */}
                <section id="home-slider">
                    <div class="container">
                        <div class="row">
                            <div class="collapse navbar-collapse">
                                <ul class="nav navbar-nav navbar-right">
                                    <li class="active"><a href="index.html"></a></li>
                                    <li class="active"><a href="#about" class="learn-btn animated fadeInUp"> <i class="fa fa-arrow-down"></i></a></li>         
                                    <li class="active"><a href="https://blog.nebulas.io/2018/04/12/creating-a-nas-wallet/" target="_blank" class="learn-btn animated fadeInUp"> <i class="fa fa-arrow-down"></i></a></li>         
                                    <li class="active"><a href="#footer" class="learn-btn animated fadeInUp"><i class="fa fa-arrow-down"></i></a></li>         
                                                        
                                </ul>
                            </div>
                            
                            <div class="main-slider">
                                <a href="index.html">
                                    <h1><img src={require("../res/images/logo.png")} alt="logo"/></h1>
                                </a>

                                <div class="slide-text" className="css_main_logo_margin_top">
                                    <p className="css_main_logo_description">
                                    /</p>
                                   
                                   <div className="css_main_logo_margin_top">
                                        {this.state.isLogin ? 
                                            this.inputBox()
                                        :
                                            <button class="css_home_button css_home_button_add " onClick={()=>{
                                                // this.props.startBtnListener();
                                                this.setState({isLogin: true});
                                            }} >GET START []</button>

                                        }
                                    </div>
                                </div>
                                {console.log("screen width = "+Utils.getScreenInfo().width)}
                                {Utils.getScreenInfo().width > 669 ?
                                <div >
                                <img src={require("../res/images/home/slider/house.png")} class="slider-house " alt="slider image"/>
                                <img src={require("../res/images/home/slider/sun.png")} class="slider-sun " alt="slider image"/>
                                <img src={require("../res/images/home/slider/birds2.png")} class="slider-birds2 " alt="slider image"/>
                                </div>
                                : <div></div>
                                }
                            </div>
                        </div>
                    </div>
                    {/* <div class="preloader"><i class="fa fa-sun-o fa-spin"></i></div> */}
                </section>

                <section id="about">
                    <div class="container" style={{marginTop: 20, minHeight:500}}>
                        <div class="row">
                            <div class="col-sm-4 text-center padding wow fadeIn" data-wow-duration="1000ms" data-wow-delay="300ms">
                                <div class="single-service">
                                    <div class="wow scaleIn" data-wow-duration="500ms" data-wow-delay="300ms">
                                        <img src={require("../res/images/home/icon1.png")} alt=""/>
                                    </div>
                                    <h2></h2>
                                    <p>/</p>
                                </div>
                            </div>
                            <div class="col-sm-4 text-center padding wow fadeIn" data-wow-duration="1000ms" data-wow-delay="600ms">
                                <div class="single-service">
                                    <div class="wow scaleIn" data-wow-duration="500ms" data-wow-delay="600ms">
                                        <img src={require("../res/images/home/icon2.png")} alt=""/>
                                    </div>
                                    <h2></h2>
                                    <p>...</p>
                                </div>
                            </div>
                            <div class="col-sm-4 text-center padding wow fadeIn" data-wow-duration="1000ms" data-wow-delay="900ms">
                                <div class="single-service">
                                    <div class="wow scaleIn" data-wow-duration="500ms" data-wow-delay="900ms">
                                        <img src={require("../res/images/home/icon3.png")} alt=""/>
                                    </div>
                                    <h2></h2>
                                    <p>/</p>
                                </div>
                            </div>
                        </div>
                    </div>
                </section>

                
                
                <footer id="footer">
                    <div class="container">
                        <div class="row">
                            <div>
                            <hr className="css_hr_color_to_transparent"></hr>
                            </div>

                            <div class="text-center bottom-separator">
                                
                                <img src={require("../res/images/home/cycle.png")} alt=""/>
                                <h1 style={{color: '#867979'}}> Android, IOS, Web, AI, BlockChain...</h1>
                            </div>
                            
                            <div class="col-md-3 col-sm-6">
                                <div class="contact-info bottom">
                                
                                    <h3 style={{color: '#867979'}}>Contacts:</h3>
                                    <h5 style={{color: '#5e5555', marginLeft: 50, }}>: andy_tocm </h5>
                                    <h5 style={{color: '#5e5555', marginLeft: 50, }}>: <a href="mailto:tocm@163.com">tocm@163.com</a> </h5>            
                                    <h5 style={{color: '#5e5555', marginLeft: 50, }}><a target="_blank" href="https://incentive.nebulas.io/cn/signup.html?invite=RiOFf"></a> </h5>
                                    
                                    
                                </div>
                            </div>
                          
                            <div class="col-sm-12">
                                <div class="copyright-text text-center">
                                    <p style={{color: '#867979'}}><a target="_blank" href="https://nebulas.io/"></a></p>
                                    <p style={{color: '#867979'}}>Copyright @2018 NasBrithday. All Rights Reserved.</p>
                                    
                                </div>
                            </div>
                        </div>
                    </div>
                </footer>
            </div>
        );
    }
}

export default Main;


